export default {
	data() {
		return {
			isCard: true,
			name:"",
			content:"",
			date:"",
			img:"",
			awatar:""
		};
	},
	methods:{
		requestData(){
			this.$api.getUserInfo().then(
				res=>{
					this.name = res.data.nickName
					this.awatar = res.data.headPortrait
				}
			)
			this.$api.getPost({
				id:"1"
			}).then(res=>{
				this.content = res.data.details
				this.date = res.data.loseTime
				this.img = res.data.image
				console.log(res)
			})
		}
	},
	created(){
			this.requestData()
	}
	
}
